
body {
  font-family: "Open Sans", serif; 
  background-image: url(/app/static/media/image.a09771fb.jpg);
  background-size: auto;
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;
  color: #fff;
}
.container {
  display: -webkit-flex;
  display: flex;
  margin: 50px auto;
  width: 100%;
  height: 1200px;

  
}
.wrapper {
display: -webkit-flex;
display: flex;
margin: auto;
margin-top: 150px;
text-align: center;
}
.title {
  padding: 30px;
}
.form {
  margin: auto;
  margin-top: 80px;
  margin-left: 100px;
}
.form input{
  width: 80%;
  padding: 10px;
  margin-top: 10px;
  border: none;
  border-radius: 10px;
  outline: none;
  background-color: rgba(84, 131, 219, 0.45);
  color:#fff;
}

::-webkit-input-placeholder{
  padding-left: 20px;
  color: #fff;
}

::-ms-input-placeholder{
  padding-left: 20px;
  color: #fff;
}

::placeholder{
  padding-left: 20px;
  color: #fff;
}
.form button{
margin-top: 15px;
width: 60%;
border: none;
padding: 10px;
border-radius: 10px;
outline: none;
background-color: rgba(43, 105, 219, 0.6);
color: #fff;
}

.info {
  margin: 80px auto 0 auto;
  width: 70%;
  font-size: 1.5em;
  font-weight: 400;
  padding: 10px;
}


@media (max-width: 1024px){
.wrapper {
  -webkit-flex-direction: column;
          flex-direction: column;
}
.form {
  margin-left: 0px;
}
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

